import * as React from 'react';
// import { Chart } from 'react-charts';

import Staff, { API, HitWindowSearchField } from '@miq/staffjs';
import { Icons, HorizontalScrollGridLayout, ViewSection, Loading, Button } from '@miq/componentjs';
import { SharedDataCtx } from '@miq/contextjs';

import { getCN, getImgUrl, Response, truncateStr, uuid } from '@miq/utiljs';
import { useDocumentTitle, useRequest } from '@miq/hookjs';
import { useSearchParams } from 'react-router-dom';

//

const TopItemCard = (props: any) => {
  const { data, top } = props;

  return (
    <div className="d-flex align-items-center" title={`${data?.name}: ${data?.count}`}>
      {data.img && (
        <div className="bg-gray-100 rounded" style={{ width: 48, maxWidth: 48 }}>
          <img
            src={getImgUrl(data.img)}
            alt={data?.name && truncateStr(data?.name, 0, 4)}
            className="rounded"
            style={{ objectFit: 'cover', width: '100%', height: 'auto', aspectRatio: '1/1' }}
          />
        </div>
      )}
      {top && (
        <div className="ms-1">
          <div className="text-ellipsis fw-bold">{data?.name}</div>
          <div className="text-muted text-sm">{data?.count} vues</div>
        </div>
      )}
    </div>
  );
};

const TopItemsCard = ({ hits = {}, itemKey = 'top_products', ...props }: any) => {
  const { title = '' } = props;

  const items = hits?.[itemKey] || [];
  if (!Boolean(items.length)) return null;

  const [item, ...rest] = items;

  return (
    <ViewSection border text={title} mainCN="d-grid gap-2">
      {item && <TopItemCard top data={item} />}
      <HorizontalScrollGridLayout className="mt-2" style={{ gridAutoColumns: 'min-content', gap: 6 }}>
        {rest && rest.map((data) => <TopItemCard data={data} key={uuid()} />)}
      </HorizontalScrollGridLayout>
    </ViewSection>
  );
};

const PerformanceCard = ({ settingSlug, ...props }: any) => {
  let [params] = useSearchParams();
  const window = params.get('__window');
  const { res, loading } = useRequest(() => API.get(`shopy-analytics/${settingSlug}/summary/`, { params }), {
    refreshDeps: [window],
  });

  if (loading) return <Loading />;

  const hitR = new Response(res);
  if (!hitR.isSuccess) return null;

  const { views_count = 0, visitors_count = 0, sent_message = 0, compare = {} } = hitR?.data;

  return (
    <Staff.Section
      title="Insights"
      actions={
        <div className="d-flex align-items-center">
          <HitWindowSearchField options={['today', 'last_7', 'last_30']} />
          <Staff.Link to="insights" className="btn btn-primary-3 ms-1">
            {`Insights>`}
          </Staff.Link>
        </div>
      }
    >
      <div className="d-grid gap-2 mb-3">
        <div className="">
          Messages: {sent_message}
          {compare?.sent_message > 0 && <span className="text-sm text-muted">/{compare.sent_message}</span>}
        </div>

        <div className="">
          Visitors: {visitors_count}
          {compare?.visitors_count > 0 && <span className="text-sm text-muted">/{compare.visitors_count}</span>}
        </div>
        <div className="">
          Page views: {views_count}
          {compare?.views_count > 0 && <span className="text-sm text-muted">/{compare.sent_message}</span>}
        </div>
      </div>

      {/* bar chart with compare value */}

      <div className="d-grid grid-md-2 gap-2">
        <TopItemsCard hits={hitR?.data} title="Top viewed products" itemKey="top_products" />
        <TopItemsCard hits={hitR?.data} title="Top viewed categories" itemKey="top_cats" />
      </div>
    </Staff.Section>
  );
};

export default function ShopStaffIndexView() {
  useDocumentTitle('Store');
  const ctx = React.useContext(SharedDataCtx);
  const { isLoaded, products, orders, cats, shopy_settings, site = {} } = ctx;
  const settingSlug = site.settings;

  if (!isLoaded) return null;

  return (
    <Staff.View
      title="Commerce"
      className="no-bg"
      back="/staff/"
      actions={
        <div className="d-flex">
          <Staff.NavLink to="notifications/" className="btn btn-round btn-secondary-3" title="Action needed">
            <Icons.Notification />
          </Staff.NavLink>

          <Staff.NavLink to="settings/" className="btn btn-round btn-secondary-3" title="Shop settings">
            <Icons.Adjustments />
          </Staff.NavLink>
        </div>
      }
    >
      <Staff.Section>
        <div className="d-grid grid-2 grid-sm-3 grid-md-4 grid-lg-6">
          {shopy_settings?.config?.has_suppliers && (
            <CardLink
              title="Supplier Orders"
              tab="./"
              link={{ to: 'orders/', text: 'View all orders', perms: ['store.view_supplierorder'] }}
            >
              <h3>{orders?.count || 0}</h3>
            </CardLink>
          )}

          <CardLink
            title="Products"
            tab="./?tab=products"
            link={{ to: 'products', text: 'View all products', perms: ['store.view_product'] }}
          >
            <h3>{products?.count || 0}</h3>
          </CardLink>

          <CardLink
            tab="./?tab=categories"
            title="Categories"
            link={{ to: 'categories', text: 'View all categories', perms: ['store.view_category'] }}
          >
            <h3>{cats?.count || 0}</h3>
          </CardLink>
        </div>
      </Staff.Section>

      {settingSlug && <PerformanceCard {...{ settingSlug }} />}

      <Staff.Section title="Products by category" className="my-2">
        {cats?.cat_count
          ?.filter((i) => i.category__name)
          ?.map(({ category__name, count }, i) => (
            <div key={i}>
              {category__name}: {count}
            </div>
          ))}
      </Staff.Section>
    </Staff.View>
  );
}

const CardLink: React.FC<{
  title: string;
  tab?: string;
  link: { to: string; text: string; perms: string[] };
}> = ({ children, title, link, tab = './' }) => {
  const { to } = link;
  return (
    <div className="card-link mb-3">
      <Staff.NavLink to={tab}>
        {title && <div className="card-link-title">{title}</div>}
        {children}
      </Staff.NavLink>

      <Staff.Link to={to} requiredPerms={link.perms}>
        {link?.text && `${link.text} »`}
      </Staff.Link>
    </div>
  );
};
